import React from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import { Home } from "./pages/Home";
import { StateHookExample } from "./pages/StateHookExample";
import { Navbar } from "./components/Navbar";
import { EffectHookExample } from "./pages/EffectHookExample";
import { Context } from "./context";
import { ReducerHookExample } from "./pages/ReducerExample";

function App() {
  const appLocation = window.location.pathname.toString();
  console.log(appLocation);
  return (
    <Context.Provider>
      <BrowserRouter
        // basename={`${appLocation}`}
        // basename={`/${window.location.toString()}`}
        basename={"/react-hooks"}
        // basename={`${window.location.pathname.toString()}`}
        // basename="/"
      >
        <h1>Simple React App in JS</h1>
        <Navbar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/state-hook-example" component={StateHookExample} />
          <Route path="/effect-hook-example" component={EffectHookExample} />
          <Route path="/reducer-hook-example" component={ReducerHookExample} />
          {/* <Redirect to="/" /> */}
        </Switch>
      </BrowserRouter>
    </Context.Provider>
  );
}

export default App;
